/*! Section 4 - Navigation Styles  */
.skip-link {
    display:none;
}
.top-bar-left ul{
    list-style: none;margin: .55rem;
}
.mainmenu {
    background: #FFF;
    border-bottom: 1px solid #056468;
    border-top: 2px solid #056468;
    padding: 5px 0;
}
.top-bar-section li:not(.has-form) a:not(.button){background: none;}
.top-bar-section ul li{background: none;}
.mobile-menu .top-bar-section ul li a{color: #fff;font-size: 1.0125rem;border-right: 1px solid #056468;border-bottom: 1px solid #fff;}
.top-bar-section ul li a{color: #000;font-size: 1.8125rem;font-weight: bold;border-right: 1px solid #056468;}

@media only screen and (max-width: 40em) { 
.top-bar {
    margin-top: 0px;
}
}
@media only screen and (min-width: 40em) and (max-width: 59em) {
    .medium-6{width: 100%}
    .header-branding{width: 50%;}
}

@media only screen and (max-width: 64.063em) { 
    .contain-to-grid {
    
    background: #000;
    }
}